/**
 * 图片上传
 */

import axios from '../axios'
import config from '../config'

const axiosAPI = function (obj) {
  return axios({
    configURL: config.filesURL,
    url: obj.url ? obj.url : '/zero-box/file',
    method: 'POST',
    params: obj.params,
    data: obj.data
  });
};


/**
 * 上传图片
 * @param {*} obj 
 */
export const uploadImg = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.files.img.uploader'
    },
    data: obj.file
  })
}