/**
 * 华龙微课模块
 * API要对应到自己的模块
 */

// 每个模块代码示例
// 1.引入config文件中对应模块的ip地址 引入 axios.js
// 2. 通过一个函数修改默认的配置文件
// 3.在API的index文件中引入此文件
// 4.调用方式  this.$api.此文件名.对应方法名
import config from '../config'
import axios from '../axios'


/**
 * API接口文档书写示例 
 */
/**
 * 第一行描述接口的作用
 * @param {Object} obj // 第二行描述对应参数的格式和对应的值和作用    * // 结尾加 * 代表必填项
 * @param {String} id 文章id
 * @param {NUmber} page 页码 要查询的第几页
 * @param {Number} pageIndex 页码索引
 */


const axiosServer = function (obj) {
  return axios({
    configURL: config.courseURL,
    url: obj.url ? obj.url : '/hl/exhibition',
    method: obj.method,
    params: obj.params,
    data: obj.data
  });
};


/**
 * 获取文章列表
 */
/**
 * 修改默认请求配置文件
 * @param {Object} obj 请求体
 *  obj.infoType {String} 获取文章的类型 text:”文本+图片” video:”视频” all:“所有” 默认：“all” *
 *  obj.pageIndex {String} 页码  *
 *  obj.pageSize {String} 获取单页数量 默认10
 *  obj.field {String} 展会id  默认 001
 */
export const getArticleList = (obj) => {
  return axiosServer({
    params: {
      api: 'zero.hl.exhibition.wangeditor.get',
      infoType: obj.infoType,
      pageIndex: obj.pageIndex,
      pageSize: obj.pageSize,
      field: '001'
    }
  });
};

/**
 * 获取单个文章详情
 * @param {Object} obj 
 *  obj.id {String} 文章或视频id *
 *  obj.openid {String} 用户id *
 */
export const getOne = (obj) => {
  return axiosServer({
    params: {
      api: 'zero.hl.exhibition.wangeditor.getOne',
      id: obj.id,
      openid: obj.openid
    }
  });
};

/**
 * 点赞
 * @param {Object} obj 请求体
 *  obj.id {String} 资源id *
 *  obj.type {String} obj.type 资源类型 img图片资源 img_text 图文资源 *
 *  obj.openid {String} obj.openid 用户 openid *
 */
export const suportArticle = (obj) => {
  return axiosServer({
    method: 'POST',
    params: {
      api: 'zero.hl.exhibition.toPraise'
    },
    data: {
      id: obj.id,
      type: obj.type,
      openid: obj.openid
    }
  });
};
