import Vue from 'vue'
import App from './App.vue'
import router from "./router"
Vue.config.productionTip = false

import Mint from 'mint-ui';
Vue.use(Mint);
import 'mint-ui/lib/style.css'

import api from './API/index.js' // 请求接口模块
import ZERO from './API/zero' // 全局公有方法
Vue.prototype.$api = api;
Vue.prototype.$zero = ZERO;

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
