import axios from '../axios'
import config from '../config'

const axiosAPI = function(obj) {
  return axios({
      configURL: config.staffOnlyURL,
      url: obj.url ? obj.url : '/hl/exhibition',
      method: obj.method,
      params: obj.params,
      data: obj.data
  });
};

export const getContact = (obj) => {  //获取联系人信息列表
  return axiosAPI({
    params:{
      api: 'zero.hl.exhibition.getContact',
      exhibitionID: obj.exhibitionID || 'HL001',
      pageIndex: obj.pageIndex,
      pageSize: obj.pageSize
    }
  })
}

export const getResource = (obj) => {    //获取图片资源
  return axiosAPI ({
    params: {
      api: 'zero.hl.exhibition.getResource',
      exhibitionID: obj.exhibitionID || 'HL001',
      type: obj.type,
      pageIndex: obj.pageIndex,
      pageSize: obj.pageSize,
      openid: obj.openid
    }
  })
}

export const setImgShow = (obj) => {   //设置图片显示
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.hl.exhibition.setImgShow'
    },
    data: {
      // openid: obj.openid,
      id: obj.id,
      show: obj.show
    }
  })
}


export const postImg = (obj) => {  //上传图片
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.hl.exhibition.postImg'
    },
    data: {
      exhibitionID: obj.exhibitionID || 'HL001',
      img: obj.img,
      type: obj.type
    }
  })
}


export const delImg = (obj) => {  //删除图片
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.hl.exhibition.delImg'
    },
    data: {
      id: obj.id
    }
  })
}

export const getLength = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.hl.exhibition.getLength',
      exhibitionID: obj.exhibitionID || 'HL001'
    }
  })
}