import axios from '../axios'
import config from '../config'

const axiosAPI = function(obj) {
  return axios({
      configURL: config.contactURL,
      url: obj.url ? obj.url : '/hl/exhibition',
      method: obj.method,
      params: obj.params,
      data: obj.data
  });
};

export const exhibitionInfo = (obj) =>{
  return axiosAPI({
    params: {
      api: 'zero.hl.exhibition.exhibitionInfo',
      exhibitionID: obj.exhibitionID || 'HL001'
    }
  })
}

export const companyDetail = () => {
  return axiosAPI({
    params: {
      api: 'zero.hl.exhibition.companyDetail'
    }
  })
}

export const postContact = (obj) =>{
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.hl.exhibition.postContact'
    },
    data: {
      exhibitionID: obj.exhibitionID || 'HL001',
      plan: obj.plan,
      intention: obj.intention,
      callCard: obj.callCard
    }
  })
}

